<template lang="html">
  <div>
    <v-text-field
          label="Title"
          box
          v-model="title"
        ></v-text-field>
    <v-textarea
      outline
      v-model="memo"
      label="What is your Todo?"
      value=""
    ></v-textarea>
    <DatePicker
      @addDatetime="addDatetime"
    />
    <v-btn v-if="mode === true" @click="listAdd">리스트 추가</v-btn>
    <v-btn v-else @click="listEdit(title, memo, index)">리스트 수정</v-btn>
  </div>
</template>

<script>
import { eventBus } from "../main"
import DatePicker from "./DatePicker"

export default {
  components: {
    DatePicker
  },
  data() {
    return {
      // test title
      title: "",
      memo: "",
      deadline: null,
      index: null,
      mode: true
    }
  },
  created() {
    eventBus.$on('listEdit', (title, memo, deadline, index) => {
      this.title = title;
      this.memo = memo,
      this.deadline = deadline,
      this.index = index,
      this.mode = false;
    })
  },
  methods: {
    listAdd() {
      if(this.title == "" || this.memo == "") {
        alert("Todo를 입력해주세요.");
      }else{
        this.$emit("listAdd", this.title, this.memo, this.deadline);
        this.title = "";
        this.memo = "";
        this.deadline = null;
      }
    },
    listEdit(title, memo, index) {
      console.log(title, memo, index);
      this.$emit("listEdit", title, memo, index);
      this.title = "";
      this.memo = "";
      this.deadline = null;
      this.mode = true;
    },
    addDatetime(deadline) {
      this.deadline = deadline;
    }
  }
}
</script>

