<template lang="html">
  <div>
    <v-card
      class="pa-3 mb-3"
      :class="{'done': list.status === 'done'}"
      v-for="(list, index) in todoList"
      :key="index"
    >
      <v-card-title primary-title>
        <div>
          <h3 class="headline mb-1">{{ list.title }}</h3>
          <p>{{ list.memo }}</p>
          <div
          v-if="list.deadline != null"
          :class="{'red lighten-3': list.deadline >= cur_date}"
          >Deadline: {{ list.deadline }}</div>
        </div>
      </v-card-title>

      <v-btn
        v-if="list.status === 'created'"
        @click="statusControl(index, 'done')"
        fab flat small color="green">
        <i class="fas fa-check"></i>
      </v-btn>
      <v-btn
        v-else
        @click="statusControl(index, 'created')"
        fab flat small color="blue">
        <i class="fas fa-redo-alt"></i>
      </v-btn>
      <v-btn
        @click="listDelete(index)"
        fab flat small color="red">
        <i class="fas fa-trash-alt"></i>
      </v-btn>
      <v-btn
        @click="listEdit(list.title, list.memo, list.deadline, index)"
        v-if="list.status === 'created'"
        fab flat small color="yellow">
        <i class="fas fa-edit"></i>
      </v-btn>

      <v-btn
        @click="listUp(index)"
        v-if=""
        fab flat small color="black">
        <i class="fas fa-arrow-up"></i>
      </v-btn>
        <v-btn
          @click=""
          v-if=""
          fab flat small color="black">
          <i class="fas fa-arrow-down"></i>
        </v-btn>

    </v-card>
  </div>
</template>

<script>
import { eventBus } from "../main"
export default {
  props: ["todoList"],
  data() {
    return {
      cur_date: new Date().toISOString().substr(0, 10)
    }
  },
  methods: {
    statusControl: function(index, status) {
      console.log(this.cur_date)
      this.$emit('statusControl', index, status);
    },
    listDelete(index) {
      this.$emit('listDelete', index);
    },
    listEdit(title, memo, deadline, index) {
      eventBus.listEdit(title, memo, deadline, index);
    },
    listUp(index) {
      this.$emit('listUp', index);
    },
    listDown(index) {
      this.$emit('listDown', index);
    }
  }
}
</script>

<style lang="css" scoped>
  .done {
    background-color: rgba(0, 0, 0, 0.5);
  }
  .done p {
    color: rgba(0, 0, 0, 0.5);
  }
</style>
