<template>
<v-container>
  <v-layout row wrap>
    <v-flex xs6 pa-2>
      <List
      :todoList="todoList"
      @statusControl="statusControl"
      @listDelete="listDelete"
      @listUp="listUp"
      @listDown="listDown"
      />
    </v-flex>
    <v-flex xs6 pa-2>
      <ListAdd
      @listAdd="listAdd"
      @listEdit="listEdit"
      />
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
import List from "./List"
import ListAdd from "./ListAdd"

export default {
  components: {
    List,
    ListAdd,
  },
  data() {
    return {
      todoList: []
    }
  },
  methods: {
    listAdd(title, memo, deadline) {
      this.todoList.push({ title: title, memo: memo, deadline: deadline, status: "created" })
    },
    statusControl(index, status) {
      this.todoList[index].status = status;
    },
    listDelete(index) {
      this.todoList.splice(index, 1);
    },
    listEdit(title, memo, index) {
      this.todoList[index].title = title;
      this.todoList[index].memo = memo;
    },
    listUp(index) {

    },
    listDown(index) {

    }
  }
}
</script>
